import * as React from 'react';
import { RedoButton as Button } from '@ali/we-toolbar';
import { Controller, environment } from '@ali/4ever-cangjie';
import { HistoryRole } from '../../utils/role';

const SHORTCUTKEY = environment.IS_MAC ? 'âŒ˜+Shift+Z' : 'Ctrl+Shift+Z / Ctrl+Y';

export interface RedoButtonProps {
  controller: Controller;
  locale: Record<string, string>;
}

export default function RedoButton(props: RedoButtonProps) {
  const { controller, locale, ...rest } = props;
  const isDisabled = !controller.query('canRedo');
  const handleClick = React.useCallback(() => controller.dispatch('redo'), [controller]);

  return (
    <Button
      {...rest}
      testid="bi-toolbar-redo"
      disabled={isDisabled}
      onClick={handleClick}
      shortcutText={SHORTCUTKEY}
      role={HistoryRole.redo}
    />
  );
}
